/*PROGRESS BAR*/
.free-progress-cont {
	width: 100%;
}

.free-progress-bar {
	display: flex;
}

.free-progress-bar>:nth-child(1)>.bar,
.free-progress-bar>:nth-child(1)>.bar>.fill {
	border-top-left-radius: 20px;
	border-bottom-left-radius: 20px;
}

.free-progress-sect {
	display: flex;
	gap: 0px;
	align-items: center;
	width: 200px;
}
.free-progress-sect.hide {
	
}
.free-progress-sect.hide>.bar,
.free-progress-sect.hide>.circle {
	background-color: transparent;
	border-top: 1px solid #dedede;
	border-bottom: 1px solid #dedede;
}
.free-progress-sect.hide>.bar>.fill,
.free-progress-sect.hide>.circle>.fill {
	background-color: #00000000 !important;
}
.free-progress-sect.hide>.bar>.text {
	color: #00000099;
	text-decoration: line-through;
}

.free-progress-sect>.bar {
	height: 5px;
	width: 100%;
	background-color: #f0f0f0;
	border-top: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}
.free-progress-sect>.bar>.text {
	font-size: 15px;
	font-weight: 400;
	font-style: italic;
}

.free-progress-sect>.circle {
	background-color: #f0f0f0;
	border: 1px solid #ccc;
	border-radius: 20px;
	height: 40px;
	width: 45px;
	overflow: hidden;
}
.free-progress-sect.complete>.bar,
.free-progress-sect.complete>.circle {
	border: 0px;
}
.free-progress-sect.complete>.bar>.fill,
.progress-sect.complete>.circle>.fill {
	height: 100%;
	width: 100%;
	background-color: rgb(0, 110, 255);
}
.free-progress-sect.complete>.bar>.text {
	font-weight: 200;
	text-decoration: line-through;
	font-size: 15px;
}

.free-progress-sect.progress>.bar>.fill {
	height: 100%;
	width: 30%;
	background-color: rgb(0, 110, 255);
	transition: 1s;
}
.free-progress-sect.progress>.bar>.fill:hover {
	width: 100%;
}
.free-progress-sect.progress>.circle>.fill.complete {
	height: 100%;
	width: 100%;
	background-color: rgb(0, 110, 255);
	transition: 1s;
}