.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.numbered-list {
	row-gap: 10px;
	display: flex;
	flex-direction: column;
}

.list-input-actions {
	display: flex;
	flex-direction: row !important;
	gap: 10px;
}
.list-input-actions>.action-button {
	width: 25px;
	height: 25px;
	line-height: 25px;
	text-align: center;
}

.float-dialogue {
	max-height: fit-content;
	min-height: fit-content;
}