#job-creator-form {
	width: 50vw;
	overflow: hidden;
}

#job-creator-form>.card-content {
	padding: 0px;
	height: calc(100% - 40px);
}

.creator-quote-info-form-item {
	display: grid;
	grid-template-columns: 30% 70%;
	width: 100%;
	font-size: larger;
	gap: 0px;
}
.creator-quote-info-form-item>.form-info-title {
	font-weight: 600;
	color: #313131;
}
.creator-quote-info-form-item>:nth-child(2) {
	font-style: italic;
}

#created-jobs-form {
	min-height: 60%;
	max-height: 60%;
	border-bottom: 1px solid #ccc;
	border-top: 1px solid #ccc;
	background-color: #fcfcfc;
	overflow-y: auto;
}

#job-create-form-actions {
	min-height: 10%;
	max-height: 10%;
	align-items: center;
	padding: 10px;
}

#quote-info-form {
	padding: 10px;
	row-gap: 5px;
	display: flex;
	flex-direction: column;
	height: 100%;
}

#quote-contract-form {
	background-color: #f0f0f0;
	margin: 10px;
	border-radius: 10px;
	border: 1px solid #ccc;
	min-height: 85%;
	max-height: 5%;
	overflow-y: hidden;
}

.creator-header {
	padding: 0px;
	text-indent: 0px;
}

#quote-contract-form>.list-cont {
	text-align: left;
	overflow-y: scroll;
	height: 85%;
}
#quote-contract-form>.list-cont>.numbered-list {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
}

#created-jobs-form>.tablerow {
	height: fit-content;
	border-bottom: 1px solid #ccc;
	cursor: pointer;
}
#created-jobs-form>.tablerow:hover {
	background-color: #f0f0f0
}

.info-warning-box {
	font-size: x-large;
	padding: 10px;
}

.info-warning-box>:first-child {
	font-weight: 600;
}

.info-warning-box>.warning-text {
	font-weight: 900;
	font-size: xx-large;
	color: #dd3030;
}

.finish-job-create {
	background-color: #3391b6;
	text-transform: uppercase;
	font-weight: 600;
}
.finish-job-create:hover {
	background-color: #287796;
}

/*Quote Scheduler*/
#quote-scheduler-card {
	max-height: 70vh;
	min-height: 70vh;
	width: 50vw;
}
#quote-scheduler-card>.card-content {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 20px;
	padding: 20px;
	height: calc(100% - 40px);
}
.contracts-list {
	width: 100%;
	height: 60%;
	overflow-y: scroll;
}
#quote-scheduler-card>.card-content>.form-info-item,
.contracts-list>.form-info-item {
	display: grid;
	grid-template-columns: 40% 60%;
	width: 100%;
	align-items: center;
	justify-content: center;
	font-size: 30px;
}
.contracts-list {
	display: flex;
	flex-direction: column;
	gap: 10px;
}
.contracts-list>.form-info-item{
	grid-template-columns: 70% 20%;
	gap: 10%;
}
#quote-scheduler-card>.card-content>.form-info-item>input,
.contracts-list>.form-info-item>input {
	display: flex;
	width: 100%;
	height: 100%;
}
.contracts-list>.form-info-item>input {
	justify-self:right;
}