#job-manager-card {
	height: 90vh;
	min-width: 95vw;
	max-width: 95vw;
}

#job-manager-card>.card-content {
	height: calc(100% - 40px);
	display: flex;
	flex-direction: column;
}

.job-info {
}

.tabs-card {
	height: 100%;
	padding: 0px;
	overflow: hidden;
	border-radius: 10px;
	border: 1px solid #ccc;
	background-color: #fafafa;
}

.tabs-card>.menu-bar {
	filter: unset;
	border-bottom: 1px solid #ccc !important;
	height: 40px;
	display: flex;
	width: 100%;
}
.tabs-card>.menu-bar>.menu-button {
	border-radius: 0px;
	height: 40px;
	line-height: 40px !important;
	border-bottom: 1px solid #ccc !important;
}
.tabs-card>.menu-bar>:last-child {
	border-right: 0px;
}
/*Jonas*/
.tabs-card>.menu-bar>:nth-child(1) {
	background-color: #4172bb;
}
.tabs-card>.menu-bar>:nth-child(1):hover,
.tabs-card>.menu-bar>:nth-child(1).selected {
	background-color: #203c68;
}
/*Manufacturer*/
.tabs-card>.menu-bar>:nth-child(2) {
	background-color: #bb417a;
}
.tabs-card>.menu-bar>:nth-child(2):hover,
.tabs-card>.menu-bar>:nth-child(2).selected {
	background-color: #70294a;
}
/*Ameren*/
.tabs-card>.menu-bar>:nth-child(3) {
	background-color: #3cb46a;
}
.tabs-card>.menu-bar>:nth-child(3):hover,
.tabs-card>.menu-bar>:nth-child(3).selected {
	background-color: #1a5330;
}
/*Financing*/
.tabs-card>.menu-bar>:nth-child(4) {
	background-color: #b93e3e;
}
.tabs-card>.menu-bar>:nth-child(4):hover,
.tabs-card>.menu-bar>:nth-child(4).selected {
	background-color: #501a1a;
}
/*Permit*/
.tabs-card>.menu-bar>:nth-child(5) {
	background-color: #7045c0;
}
.tabs-card>.menu-bar>:nth-child(5):hover,
.tabs-card>.menu-bar>:nth-child(5).selected {
	background-color: #2f1a55;
}
/*PV Maintenance*/
.tabs-card>.menu-bar>:nth-child(6) {
	background-color: #dba531;
}
.tabs-card>.menu-bar>:nth-child(6):hover,
.tabs-card>.menu-bar>:nth-child(6).selected {
	background-color: #7a5f22;
}
/*.tabs-card>.menu-bar>.menu-button.selected {
	background-color: #ccc;
	font-weight: 700;
}*/

.job-info-consultant {
	font-size: larger;
	font-weight: 550;
	color: #313131;
}
.job-info-progress {
	display: flex;
	gap: 10px;
}
.job-info-progress {
	font-size: large;
}
.job-info-progress>.progress {
	font-style: italic;
	font-weight: 550;
}
.job-info-perccomplete {
	font-style: italic;
	font-weight: 550;
	font-size: x-large;
}
#scheduled-date-form-item-cont {
}
#scheduled-date-form-item-cont>.form-info-title {
	font-size: x-large;
}
#scheduled-date-form-item-cont>input {
	height: 40px;
	width: 50%;
	background-color: #fafafa;
}
#scheduled-date-form-item-cont>input:focus {
	background-color: #ccc;
}

.job-manager-form-cont {
	display: grid;
	flex-direction: column;
	width: 100%;
	height: calc(100% - 40px);
	/*grid-template-columns: 50% 50%;*/
	row-gap: 10px;
	gap: 10px;
	overflow-y: auto;
	overflow-x: hidden;
}
.job-manager-form-items-cont>.form-info-item {
	padding: 10px;
	padding-left: 20px;
	padding-right: 20px;
	display: grid;
	grid-template-columns: 40% 60%;
	align-items: center;
	justify-content: center;
}
.installers-cont {
	padding-left: 10px;
	padding-right: 10px;
}
#installers-form-item-cont {
	display: grid;
	grid-template-columns: 40% 60%;
	gap: 0;
	align-items: start;
	justify-content: end;
}
#installers-form-item-cont>.list-input-cont {
	display: flex;
	justify-items: end;
}
#installers-form-item-cont>.list-input-cont>.list-input-actions {
	justify-content: end;
}
#installers-form-item-cont>.list-input-cont>:nth-child(2) {
	justify-content: end;
	justify-self: end;
	text-align: right;
}
#installers-form-item-cont>.list-input-cont>:nth-child(2)>.numbered-list {
	align-items: end;
	height: 6rem;
	overflow-y: scroll;
	padding: 10px;
	border-radius: 10px;
	border: 1px solid #ccc;
	background-color: #f7f7f7;
}
#installers-form-item-cont>.list-input-cont>:nth-child(2)>.numbered-list>.list-item {
	display: flex;
	flex-direction: row;
	gap: 10px;
}
.job-manager-form-items-cont>.form-info-item>:nth-child(2),
#installers-form-item-cont>:nth-child(2) {
	width: 100%;
}
.job-manager-form-items-cont>.form-info-item>input,
#installers-form-item-cont>:nth-child(2)>.list-input-actions>input {
	padding: 10px;
	width: 80%;
}
#installers-form-item-cont>:nth-child(2)>.list-input-actions>input {
	background-color: #fafafa;
}
#installers-form-item-cont>:nth-child(2)>.list-input-actions>input:focus {
	background-color: #ccc;
}
#installers-form-item-cont>.form-info-title {
	font-size: x-large;
	font-weight: 550;
	color: #212121;
	font-style: italic;
}
.job-manager-form-items-cont>.form-info-item>.form-info-title {
	font-size: larger;
	font-weight: 550;
	color: #212121;
	font-style: italic;
}

.form-item-header {
	grid-template-columns: 100% 0% !important;
}

.form-item-header>.form-info-title {
	font-size: xx-large !important;
	color: #000 !important;
	font-weight: 900;
	width: 100%;
	font-style: normal !important;
}
.job-information {
	display: grid;
	grid-template-columns: 33% 33% 33%;
}
#job-manager-controls {
	padding-top: 10px;
}

.job-info-base {
	width: 100%;
}

.job-info-cust {
	display: grid;
	grid-template-columns: calc(50% - 5px) calc(50% - 5px);
	gap: 10px;
	border: 1px solid #ccc;
	padding: 10px;
	background-color: #f7f7f7;
	border-radius: 10px;
	font-size: large;
}
.job-info-cust>.form-info-item {
	display: grid;
	grid-template-columns: 30% 70%;
	gap: 0%;
}
.job-info-cust>.form-info-item>.form-info-title {
	font-weight: 600;
}
.job-info-cust>.form-info-item>input {
	width: 100%;
}

.job-info-base>.form-info-item {
	align-items: center;
	justify-content: left;
	gap: 10px;
}
.job-info-base>.form-info-item>.form-info-title {
	font-size: larger;
	font-weight: 600;
}

/*Close dialogue*/
.close-job-text {
	font-size: x-large;
	font-weight: 600;
	padding: 10px;
}

#close-job-button {
	background-color: #1f643a;
	font-size: 25px;
	font-weight: 600;
}

#close-job-button:hover {
	background-color: #19502e;
}

#job-manager-close-controls>.action-button {
	height: 50px;
	line-height: 50px;
	font-size: 25px;
}

#go-back-job-man-button {
	font-weight: 300;
	font-size: 20px !important;
}

.incomplete-fields {
	font-size: 35px;
}

.inc-fields-field {
	font-size: 30px;
	font-weight: 500;
	font-style: italic;
	padding: 10px;
}