#job-editor-card {
	width: 50vw;
	height: 70vh;
}

#job-editor-card>.card-content {
	height: calc(100% - 40px);
}
#job-editor-card>.card-content>.editor-form {
	height: 95%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 3%;
}
.editor-form>.form-info-item {
	width: 100%;
	display: grid;
	grid-template-columns: 40% 60%;
	justify-content: center;
	align-items: center;
	height: 100%;
}
.editor-form>.form-info-item>input {
	width: 100%;
	height: 35px;
}
.editor-form>.form-info-item>.form-info-title {
	font-size: 20px;
	height: min-content;
}