/*PROGRESS BAR*/
.progress-cont {
	width: 100%;
}

.progress-bar {
	display: flex;
}

.progress-bar>:nth-child(1)>.bar,
.progress-bar>:nth-child(1)>.bar>.fill {
	border-top-left-radius: 20px;
	border-bottom-left-radius: 20px;
}

.progress-sect {
	display: flex;
	gap: 0px;
	align-items: center;
	width: 200px;
}

.progress-sect>.bar {
	height: 5px;
	width: 100%;
	background-color: #f0f0f0;
	border-top: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}
.progress-sect>.bar>.text {
	font-size: 15px;
	font-weight: 400;
	font-style: italic;
}

.progress-sect>.circle {
	background-color: #f0f0f0;
	border: 1px solid #ccc;
	border-radius: 20px;
	height: 40px;
	width: 45px;
	overflow: hidden;
}
.progress-sect.complete>.bar,
.progress-sect.complete>.circle {
	border: 0px;
}
.progress-sect.complete>.bar>.fill,
.progress-sect.complete>.circle>.fill {
	height: 100%;
	width: 100%;
	background-color: rgb(0, 110, 255);
}
.progress-sect.complete>.bar>.text {
	font-weight: 200;
	text-decoration: line-through;
	font-size: 15px;
}

.progress-sect.progress>.bar>.fill {
	height: 100%;
	width: 30%;
	background-color: rgb(0, 110, 255);
	transition: 1s;
}
.progress-sect.progress>.bar>.fill:hover {
	width: 100%;
}
.progress-sect>.bar>.text {
	position: relative;
	top: 10px;
}
.progress-sect.progress>.bar>.text {
	top: -40px;
	font-size: 25px;
	font-weight: 600;
}
/*Info message customization*/
.progress-sect>.bar>.msg {
	margin-top: -20px;
	display: flex;
	align-items: center;
	gap: 5px;
}

.progress-sect>.bar>.msg>img {
	height: 20px;
	filter:invert(30%) sepia(41%) saturate(7477%) hue-rotate(337deg) brightness(99%) contrast(90%);
}
.progress-sect>.bar>.msg>.msg-text {
	font-size: 16.5px;
	font-style: italic;
	font-weight: 600;
	color: #f0314b;
}
/*Colors*/
.progress-bar.red>.progress-sect.complete>.bar>.fill,
.progress-sect.complete>.circle>.fill {
	height: 100%;
	width: 100%;
	background-color: rgb(216, 79, 69);
}

.progress-bar.red>.progress-sect.progress>.bar>.fill {
	height: 100%;
	width: 30%;
	background-color: rgb(216, 79, 69);
	transition: 1s;
}