.job-log-cont {
	height: calc(100% - 100px);
	top: 100px;
	width: 100%;
}
.job-log-cont>.action-row {
	align-items: center;
	margin-left: 25px;
}
.job-log-cont>.action-row>.header>div {
	font-size: 40px;
	color: #272727;
	font-weight: 550;
}
.job-log-cont>.table-container {
	padding: 10px;
}
#log-filter-card {
	z-index: 60 !important;
	min-width: 99%;
	max-width: 99%;
	min-height: unset;
	height: min-content;
}
#log-filter-card>.card-content {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.filter-form-item {
	align-items: center;
	justify-content: center;
	gap: 20px;
	font-size: large;
}
.filter-form-item>.form-info-title {
	font-weight: 600;
}

.log-action-button {
	height: 100% !important;
	width: 150px;
	line-height: 25px;
	background-color: #2f83c7;
}
.log-action-button:hover {
	background-color: #266ca5;
}

.joblog-table {
	height: calc(100% - 40px) !important;
	overflow-y: auto;
}

.job-log-card>.card-content {
	padding: 0px;
	height: 100%;
}
.job-log-card {
	height: 95%;
	min-width: 100%;
	overflow: hidden;
}
.job-log-card>.card-content>.tablerow.header {
	background-color: #062a49;
	color:#fff;
	border-top-right-radius: 7.5px;
	border-top-left-radius: 7.5px;
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: 14% 14% 14% 50% 8%;
}

.job-log-card>.card-content>.tablerow.header>div {
	padding: 0;
	margin: 0;
	line-height: 40px;
	font-size: 20px;
	background-color: transparent;
	font-weight: 600;
}

.joblog-table>.tablerow {
	height: fit-content;
	display: grid;
	grid-template-columns: 14% 14% 14% 50% 8%;
	align-items: center;
	justify-content: center;
	border-bottom: 1px solid #ccc;
}
.joblog-table>.tablerow>.action-button {
	width: 40px;
	height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: transparent;
	filter: invert();
}
.joblog-table>.tablerow>.action-button:hover {
	filter: var(--drop-shadow);
}
.joblog-table>.tablerow:hover {
	background-color: #f9f9f9;
}
.joblog-table>:last-child {
	border-bottom: 0px;
}
.joblog-table>.tablerow>.row-item {
	font-size: 20px;
}
.drop-down-search{
	width: fit-content;
}

.drop-down-search>.dropdown-cont {
	min-width: 200px
}

@media screen and (max-width: 1600px) {
	#log-filter-card {
		height: 20%;
	}
	#log-filter-card>.card-content>.action-row {
		height:100%;
		width: 100%;
		display: grid;
		grid-template-columns: repeat(3, 30%) !important;
		gap: 0px;
		align-items: center;
		justify-content: start;
	}
	#log-filter-card>.card-content>.action-row>.filter-form-item {
		display: grid;
		grid-template-columns: 35% 60% !important;
		gap: 0px;
	}
	#log-filter-card>.card-content>.action-row>.filter-form-item>input {
		width: 100%;
	}
	#log-filter-card>.card-content>.action-row>.filter-form-item>.form-info-title {
		width: 100%;
	}
	#log-filter-card>.card-content>.action-row>.filter-form-item>.dropdown-cont {
		max-width: 100%;
		min-width: unset;
	}
	.drop-down-search {
		width: unset;
	}

	.table-container {
		width: 100%;
		height: 90%;
	}
}

#chart-row {
	width: 100%;
	margin: 0px;
	padding: 0px;
}