#submitted-quotes-table {
	width: 95%;
	height: 95%;
	overflow: hidden;
	margin-left: 2.5%;
}
#submitted-quotes-header {
	background-color: #f0f0f0;
	margin: 0px;
	padding: 0px;
	border-bottom: 1px solid #ccc;
}
#submitted-quotes-header>div {
	background-color: transparent;
	margin: 0px;
	padding: 10px;
	border-right: 1px solid #ccc;
	font-weight: 600;
}
#submitted-quotes-header>:last-child {
	border-right: 0px;
}
#submitted-quotes-table>.card-menubar {
	position: relative;
}
#submitted-quotes-table>.card-content {
	padding: 0px;
	height: calc(100% - 40px);
}
#submitted-quotes-table-cont {
	padding: 0px;
	height: calc(100% - 40px);
	overflow-y: scroll;
}
#submitted-quotes-header,
#submitted-quotes-table-cont>.tablerow {
	display: grid;
	grid-template-columns: repeat(3, 33%);
	align-items: center;
	justify-content: center;
}
#submitted-quotes-table-cont>.tablerow:hover {
	background-color: #f9f9f9;
}
#submitted-quotes-table-cont>.tablerow {
	padding: unset;
	height: fit-content;
	width: 100%;
	border-bottom: 1px solid #ccc;
	padding: 10px;
}
#submitted-quotes-table-cont>:last-child {
	border-bottom: 0px;
}
#submitted-quotes-table-cont>.tablerow>div {
	margin: 0px;
	padding: 0px;
	height: 100%;
}

#submitted-quotes-table-cont>.tablerow>.action-button {
	height: 40px;
	line-height: 40px;
	width: fit-content;
	padding-left: 10px;
	padding-right: 10px;
	align-self: center;
	justify-self: center;
}