#pv-maintenance-table-card {
	min-width: calc(100% - 10px);
	height: calc(100% - 50px);
	overflow: hidden;
}
#pv-maintenance-table-card>.card-content {
	padding: 0px;
	height: calc(100% - 40px);
}
#pv-maintenance-table-card>.card-content>.tablerow.header {
	background-color: #062a49;
	margin: 0px;
	padding: 0px;
}
#pv-maintenance-table-card>.card-content>.tablerow.header>div {
	padding: 0px;
	margin: 0px;
	background-color: transparent;
	color:#fff;
	font-size: x-large;
	line-height: 40px;
}

#pv-maintenance-table-card-card-content>.card-content.data-table {
	padding: 0px;
	height: 100%;
	overflow-y: scroll;
}

#pv-maintenance-table-card-card-content>.card-content.data-table>.tablerow {
	height: fit-content;
	padding: 10px;
	margin: 0px;
	border-bottom: 1px solid #ccc;
	cursor: pointer;
}
#pv-maintenance-table-card-card-content>.card-content.data-table>.tablerow:hover {
	background-color: #f0f0f0
}
#pv-maintenance-table-card-card-content>.card-content.data-table>.tablerow>div {
	margin: 0px;
	padding: 0px;
}




#pv-notes {
	background-color: #fafafa;
	min-height: 40%;
	max-height: 40%;
	overflow-y: scroll;
	padding: 10px;
	font-size: larger;
	text-align: left;
	min-width: 100%;
	max-width: 100%;
	border-radius: 10px;
	border: 1px solid #ccc;
}

#pv-maint-editor {
	height: fit-content;
	width: 60vw;
}
#pv-maint-editor>.card-content {
	height: calc(100% - 40px);
	padding: 0px;
}
#pv-maint-editor>.card-content>.card-content {
	display: grid;
	grid-template-columns: 55% 45%;
	height: 70%;
}
#pv-maint-editor>.card-content>.action-row {
	height: 10% !important;
	padding: 10px;
}

#pv-filter-popout {
	width: 70vw;
	height: 80vh;
}
#pv-filter-popout>.card-content {
	height: calc(100% - 40px);
	padding: 0px;
}
#pv-filter-popout>.card-content>.card-content {
	height: 90%;
	padding: 20px;
}
#pv-filter-popout>.card-content>.action-row {
	height: 10%;
	align-items: center;
	padding: 10px;
}

#set-filters {
	height: fit-content;
}

#optional-filters {
	height: 40%;
}

#more-opts-filters-text {
	text-align: left;
	text-indent: 20px;
	font-size: x-large;
	font-style: italic;
}